.login {
  background-color: #f2f2f2;
  display: grid;
  place-items: center;
  height: 100vh;
}

.login__container {
  display: flex;
  flex-direction: column;
}

.login__container > img {
  object-fit: contain;
  width: 200px;
  height: 150px;
}

.login__container > Button {
  margin: 15px 0;
}
